*{
  font-family: Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  margin:0;
  padding: 0;
}

body{
  padding: 60px;
}
input{
  box-shadow: 3px 3px 6px 1px #666;
  border:1px solid #999;
  border-radius: 3px;
  color:#333;
  font-size: 1.3rem;
  line-height: 2rem;
  margin-bottom: 10px;
  padding: 5px;
  width: 100%;
}

h3{
  line-height: 1.5rem;
}
.title{
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.category{
  font-size: 2rem;
  margin-top: 20px;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}





.card{
  align-content: center;
  border:2px solid #333;
  border-radius: 5px;
  margin: 10px auto;
  overflow: hidden; 
  background-color: #333;
  color:white;
  box-shadow: 5px 5px 10px 1px #666;
  max-width: 100%;
}

.card img{
  width:100%;
  }

.card p {
  padding: 5px;
  font-size: 1.25rem;
}

